import React, { Suspense, lazy } from "react";
import { Layout } from "../../components/layout";
import { SEO } from "../../components/seo";

// Could also use `react-loadable`
// See https://github.com/gatsbyjs/gatsby/issues/13274#issuecomment-481871564
const LoadablePlot = lazy(() => import("../../components/skills/plot"));

const SkillsPlotsPage = () => (
  <Layout>
    <SEO title="Skill Plots" />
    <Suspense fallback={<div>Loading...</div>}>
      <LoadablePlot />
    </Suspense>
  </Layout>
);

export default SkillsPlotsPage;
